.calculator {
    width: 500px;
    margin: auto 35%;
  }
  
  .return-section {
    background-color: #575757;
    padding: 10px;
    color: #fff;
    text-align: right;
    font-size: 30px;
  }
  
  .digits-elts {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
  }
  
  .button-section {
    padding: 20px;
    cursor: pointer;
    border: 1px solid #dadada;
  }
  
  .button-section:hover {
    background-color: #e9e9e9;
  }
  
  .operator {
    background-color: #ff9100fd;
  }
  
  .operator:hover {
    background-color: #f08c0a;
  }
  
  .end {
    display: grid;
    grid-template-columns: 50% 25% 25%;
  }